export default {
    middleware: 'auth',
    auth: 'guest',
    layout: 'none',
    
    data () {
        return {
            userName: '',
            email: '',
            password: ''
        }
    },

    methods: {
        async onSignUp () {
            try {
                let data = {
                    userName: this.userName,
                    email: this.email,
                    password: this.password
                }

                let response = await this.$axios.$post('/api/auth/signup', data)
                console.log(response)

                if (response.success) {
                    // await this.$auth.loginWith('local', {
                    //     data: {
                    //         email: this.email,
                    //         password: this.password
                    //     }
                    // })
                    

                    this.$router.push('/')
                }
            } catch (error) {
                console.log(error)
            }
        }
    }
}
